//  style
@import "../../base/variables/options";
@import "../../base/mixins/clearfix";
@import "../../responsive/mixin";
@import "mixin";

// 
.container {
  @include make-container();
  @if $enable-responsive {
    width: 100%;
    @include make-container-max-widths();
  } @else {
    max-width: 100%;
  }
}

// 
//  100%  pc 
.container-fluid {
  width: 100%;
  @include make-container();
}



