//  12 
@mixin box-column($columns) {

  .box-item {
    flex-basis: 0;
    flex-grow:  1;
    align-self: stretch;
  }

  @for $i from 1 through $columns {
    .box-item-#{$i} {
      flex:       0 0 percentage($i / $columns);
      //  align-items 
      // 
      align-self: stretch;
    }
  }
}



