@mixin hover {
  &:hover { @content }
}

// sass-lint:disable brace-style
@mixin hover-focus {
  &:focus,
  &:hover {
    @content
  }
}

//  hover focus 
@mixin plain-hover-focus {
  &,
  &:focus,
  &:hover {
    @content
  }
}

// hover focus active 
@mixin hover-focus-active {
  &:focus,
  &:active,
  &:hover {
    @content
  }
}



