// box layout  Horizontal Box  Vertical Box
@import "mixin";
@import "variable";

.box {
  display: flex;

  // Horizontal Box
  &-h {
    flex-direction: row;
  }

  // Vertical Box
  &-v {
    flex-direction: column;
  }
}

@include box-column($box-columns);




