@import "../../../shadow/mixin";
@import "../../../theme/mixin";
@import "../../../animation/mixin";
@import "../../../animation/variable";
@import "variable";

//  .list  .list-item 
.list-menu {
  position:         absolute;
  z-index:          3;
  box-sizing:       border-box;
  display:          none;
  min-width:        $list-menu-min-width;
  max-width:        calc(100vw - 32px);
  max-height:       calc(100vh - 32px);
  border-radius:    2px;
  background-color: #fff;
  white-space:      nowrap;
  opacity:          0;
  transform:        scale(0);
  transform-origin: top left;
  will-change:      transform, opacity;
  @include shadow(2);

  &:focus {
    outline: none;
  }

  // 
  &-open {
    display:   inline-block;
    opacity:   1;
    transform: scale(1);
  }

  // 
  &-animating {
    display:    inline-block;
    overflow-y: hidden;
    transition: opacity $list-menu-fade-duration $animation-ease-out;
  }

  // 
  &-items {
    box-sizing:       border-box;
    background-color: #fff;
    will-change:      transform;

    // 
    > * {
      opacity: 0;
    }

    > .list-item {
      padding-top:    $list-menu-item-padding-y;
      padding-bottom: $list-menu-item-padding-y;
      cursor:         pointer;
      &:hover {
        background-color: $list-menu-item-hover-bg;
      }
      &.active {
        background-color: $list-menu-item-active-bg;
      }
    }

    // scss  .list-menu-animating .list-menu-items
    .list-menu-animating & {
      //  overflow-y  hidden
      overflow-y: hidden;

      > * {
        transition: opacity $list-menu-item-fade-duration $animation-ease-out;
      }
    }

    // scss  .list-menu-open .list-menu-items > *
    .list-menu-open & {
      > * {
        opacity:     1;
        will-change: opacity;
      }
    }
  }

  //  transform-origin
  &-top-left {
    transform-origin: top left !important;
  }

  &-top-right {
    transform-origin: top right !important;
  }

  &-bottom-left {
    transform-origin: bottom left !important;
  }

  &-bottom-right {
    transform-origin: bottom right !important;
  }

}

// 
.list-menu-cascaded {
  @include shadow(0);

  .list-menu-items {
    min-width: $list-menu-min-width;
    @include shadow(2);
  }

  .list-item {
    position:         relative;
    background-color: transparent;

    // 
    &[aria-level="branch"] {
      padding-right: 1.75rem;

      &::after {
        content:      "";
        position:     absolute;
        top:          50%;
        right:        1rem;
        z-index:      1;
        width:        .5rem;
        height:       .5rem;
        border-top:   1px solid;
        border-right: 1px solid;
        transform:    translateY(-50%) rotate(45deg);
        will-change:  border-color;
        @include theme-prop(border-color, hint-on-light);
        @include animation-transition(border-color);
      }
    }
    > .list {
      position:         absolute;
      top:              0;
      left:             100%;
      visibility:       hidden;
      transform:        scaleX(0);
      transform-origin: left;
      will-change:      visibility, transform;
      @include animation-transition(visibility);
      @include animation-transition(transform);
    }

    &:hover {
      // 
      &::after {
        @include theme-prop(border-color, primary-on-light);
      }
    }
  }

  //  aria-controls="click"
  & .list-item.expanded,
  &:not([aria-controls="click"]) .list-item:hover {
    > .list {
      visibility: visible;
      transform:  scaleX(1);
    }
  }
}



